@import url('https://fonts.googleapis.com/css?family=Work+Sans&display=swap');
body {
	font-family: 'Work Sans', sans-serif;
}

.Pokecard {
	width: 16.5%;
	padding: 2.5%;
	margin: 1rem;
	box-shadow: 7px 10px 12px -2px rgba(0, 0, 0, 0.56);
	background-color: #fff;
	border-radius: 4px;
	cursor: pointer;
	text-align: center;
	transition: .3s ease-out;
	opacity: .85;
}

.Pokecard-img {
	width: 100%;
	transition: .3s ease-out;
}

.Pokecard:hover .Pokecard-img {
	opacity: 1;
	transform: scale(1.06);
	transition-duration: .3s;
	filter: drop-shadow(2px 4px 6px grey);
}

.Pokecard:hover {
	opacity: 1;
	filter: drop-shadow(2px 4px 6px grey);
}
